@import 'mixins';

.welcome-wrapper{
    height: 100vh;
    width: 100%;
    position: relative;
    background: $blue;
    & .left-column{
        width:33.33%;
        height: 100%;   
        position: relative;   
        & .logo{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
        }
    }
    & .right-column{
        width:66.66%;
        position: absolute;
        top:0;
        left: 33.33%;
        height: 100%;
        & .first-box{
            position: absolute;
            width: 100%;
            height: 50%;
            overflow: hidden;
            background: $white;
        }
        & .second-box{
            position: absolute;
            width: 100%;
            height: 50%;
            overflow: hidden;
            top:50%;
            background: $black;
        }

        & .link-box{
            position: absolute;
            left: 50px;
            bottom: 50px;
            & .link-icon{
                color: $white;
                display: inline-block;
                vertical-align: top;
                padding-right: 20px;
            }
            & .link-title{
                color: $white;
                font-size: 30px;
            }
            & .link-button{
                background: $white;
                color: $orange;
                font-size: 16px;
                font-weight: $bold;
                padding: 8px 0;
                width:300px;
                text-align: center;
                text-transform: uppercase;
                margin-top: 20px;
                display: inline-block;
            }
        }
    }
}